import React, {useContext} from 'react';
import {Context} from "../index";
import {Button, Container, Nav, Navbar, NavLink} from "react-bootstrap";
import {ADMIN_ROUTE, BASKET_ROUTE, LOGIN_ROUTE, SHOP_ROUTE} from "../utils/consts";
import {observer} from "mobx-react-lite";
import {useHistory} from "react-router-dom";

const NavBar = observer(() => {
    const {user} = useContext(Context)
    const history = useHistory()
    return (
        <Navbar bg="dark" variant="dark">
            <Container>
                <NavLink style={{color: 'white'}} onClick={() => history.push(SHOP_ROUTE)} to={SHOP_ROUTE}>BoysBuy</NavLink>
                {user.isAuth ?
                    <Nav className='ml-auto' style={{color: 'white'}}>
                        <Button variant={"outline-light"} onClick={() => history.push(ADMIN_ROUTE)}></Button>
                        <Button variant={"outline-light"} onClick={() => history.push(LOGIN_ROUTE)} className='mx-2'></Button>
                        {/*<Button variant={"outline-light"} onClick={() => history.push(BASKET_ROUTE)}></Button>*/}
                    </Nav>
                :
                    <Nav className='ml-auto' style={{color: 'white'}}>
                        <Button variant={"outline-light"} onClick={() => user.setIsAuth(true)}></Button>
                    </Nav>
                }

            </Container>
        </Navbar>
    );
});

export default NavBar;