import {makeAutoObservable} from "mobx";

export default class DeviceStore {
    constructor() {
        this._types = [
            {id: 1, name: ''},
            {id: 2, name: ''},
            {id: 3, name: ''},
            {id: 4, name: ''},
        ]
        this._brands = [
            {id: 1, name: 'Samsung'},
            {id: 2, name: 'Apple'},
            {id: 3, name: 'Lenovo'},
            {id: 4, name: 'Asus'},
            {id: 5, name: 'Acer'},
            {id: 6, name: 'LG'},
        ]
        this._devices = [
            {id:1, name: 'Iphone 12 pro', price: 25000, rating: 5, img: 'https://content2.onliner.by/catalog/device/main/01e04bb3519e9f859244d8df82104230.jpeg'},
            {id:2, name: 'Iphone 12 pro', price: 25000, rating: 5, img: 'https://content2.onliner.by/catalog/device/main/01e04bb3519e9f859244d8df82104230.jpeg'},
            {id:3, name: 'Iphone 12 pro', price: 25000, rating: 5, img: 'https://content2.onliner.by/catalog/device/main/01e04bb3519e9f859244d8df82104230.jpeg'},
            {id:4, name: 'Iphone 12 pro', price: 25000, rating: 5, img: 'https://content2.onliner.by/catalog/device/main/01e04bb3519e9f859244d8df82104230.jpeg'},
            {id:5, name: 'Iphone 12 pro', price: 25000, rating: 5, img: 'https://content2.onliner.by/catalog/device/main/01e04bb3519e9f859244d8df82104230.jpeg'},
            {id:6, name: 'Iphone 12 pro', price: 25000, rating: 5, img: 'https://content2.onliner.by/catalog/device/main/01e04bb3519e9f859244d8df82104230.jpeg'},
            {id:7, name: 'Iphone 12 pro', price: 25000, rating: 5, img: 'https://content2.onliner.by/catalog/device/main/01e04bb3519e9f859244d8df82104230.jpeg'},
            {id:8, name: 'Iphone 12 pro', price: 25000, rating: 5, img: 'https://content2.onliner.by/catalog/device/main/01e04bb3519e9f859244d8df82104230.jpeg'}
        ]
        this._selectedType = {}
        this._selectedBrand = {}
        makeAutoObservable(this)
    }

    setTypes(types) {
        this._types = types
    }

    setBrands(brands) {
        this._brands = brands
    }

    setDevices(devices) {
        this._devices = devices
    }

    setSelectedType(type) {
        this._selectedType = type
    }

    setSelectedBrand(brand) {
        this._selectedBrand = brand
    }

    get types() {
        return this._types
    }

    get brands() {
        return this._brands
    }

    get devices() {
        return this._devices
    }

    get selectedType() {
        return this._selectedType
    }

    get selectedBrand() {
        return this._selectedBrand
    }
}