import React from 'react';
import {Button, Card, Col, Container, Image, Row} from "react-bootstrap";
import star from '../assets/Star 1.png'

const DevicePage = () => {
    const device =  {id:1, name: 'Iphone 12 pro', price: 25000, rating: 5, img: 'https://content2.onliner.by/catalog/device/main/01e04bb3519e9f859244d8df82104230.jpeg'}
    const description = [
        {id:1, title: ' ', description: '5'},
        {id:2, title: '', description: '12 '},
        {id:3, title: '', description: ' 3'},
        {id:4, title: ' ', description: '2'},
        {id:5, title: '', description: '4000'},
    ]

    return (
        <Container className='mt-2'>
            <Row>
                <Col md={4}>
                    <Image width={300} height={300} src={device.img}></Image>
                </Col>
                <Col md={4}>
                    <Row className='d-flex flex-column align-items-center justify-content-center'>
                        <h2 className='d-flex justify-content-center'>{device.name}</h2>
                        <div
                            style={{background: `url(${star}) no-repeat center center`, width:240, height:240, backgroundSize: 'cover', fontSize:64}}
                            className='d-flex align-items-center justify-content-center'
                        >
                            {device.rating}
                        </div>
                    </Row>
                </Col>
                <Col md={4}>
                    <Card
                        className='d-flex flex-column justify-content-around align-items-center'
                        style={{width:300, height:300, fontSize: 32, border: '5px solid lightgray'}}
                    >
                        <h3>: {device.price} </h3>
                        <Button className='d-flex' variant={"outline-dark"}>  </Button>
                    </Card>
                </Col>
            </Row>
            <Row className='d-flex flex-column m-3'>
                <h1></h1>
                {description.map((info, index) =>
                    <Row key={info.id} style={{background: index % 2 === 0 ? 'lightgray' : 'transparent', padding: 10}}>
                        {info.title}: {info.description}
                    </Row>
                )}
            </Row>
        </Container>
    );
};

export default DevicePage;